@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background: url(/static/media/1.d25d07f1.jpg);
}

.Title {
  background-color: purple;
  color: blanchedalmond;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  border-radius: 12px;
}
.Title-back {
  text-align: center;
  width: 50%;
  margin: auto;
}

.form-datas {
  margin-left: 25px;
  color: rgb(250, 231, 196);
}
.header {
  border-radius: 12px;
  width: 50%;
  color: yellow;
  padding-left: 10px;
  text-decoration: underline;
}

.form-data label {
  font-weight: bolder;
  font-size: 25px;
}
.form-data select {
  border-radius: 10px;
  font-size: 22.5px;
  font-weight: bold;
  box-shadow: black;
  background-color: rgb(238, 230, 217);
}
.form-data input {
  border-radius: 10px;
  font-size: 22.5px;
  font-weight: bold;
  box-shadow: black;
  background-color: rgb(238, 230, 217);
}
.form-action {
  text-align: right;
  padding-right: 25px;
}
.form-action input {
  font: inherit;
  font-weight: bold;
  padding: 0.5rem;
  margin: 5px;
  border-radius: 6px;
  border: 2px solid black;
  background-color: purple;
  color: whitesmoke;
  width: 10rem;
}
.form-action input:hover {
  color: black;
  background-color: rgb(177, 22, 177);
}

.card {
  border-radius: 12px;
  background: url(/static/media/2.96201be1.jpg);
  width: 75%;
  margin: auto;
}

.res-area {
  text-align: center;
  border-radius: 12px;
  background-color: black;
  color: wheat;
  width: 50%;
  margin: auto;
}

